import React from 'react';
import './TodoItem.css';

function TodoItem(props) {
    const onComplete = () => {
        alert('Completada: ' + props.text)
    }
    const onDelete = () => {
        alert('Eliminaste: ' + props.text)
    }
    return (
        <li className="TodoItem">
            <span 
            className={`Icon IconCheck ${props.completed && 'IconCheckActive'}`}
            onClick={onComplete}>
                âˆš
            </span>
            <p className={`TodoItemP ${props.completed && 'TodoItemP-completado'}`}>
                {props.text}
            </p>
            <span 
            className="Icon IconDelet"
            onClick={onDelete}>
                X
            </span>
        </li>
    );
}

export {TodoItem}; 